import React, { Component } from 'react';
import InputSlider from 'react-input-slider';

class Controls extends Component {
	state = {
		sliderRed: 1,
		sliderGreen: 1,
		sliderBlue: 1,
		sliderFlame: 0,
		hwSelect: 'stock',
		hwStock: true,
		hwBlack: false,
		pupNeck: 'stock',
		pupNeckStock: true,
		pupNeckBlack: false,
		pupNeckRails: false,
		pupNeck90: false,
		pupBridge: 'stock',
		pupBridgeStock: true,
		pupBridgeBlack: false,
		pupBridgeRails: false,
		pupBridge90: false
	};
	/*state = {
		color: {
			red: 100,
			green: 100,
			blue: 100,
			alpha: 0.8
		},
		neck: 'stock',
		bridge: 'stock',
		hardware: 'stock',
		//color: [100,100,100,0.8] //r,g,b,a
	};

	handleRed = (e) =>{
		var c = {
			red: e.target.value,
			green: this.state.color.green,
			blue: this.state.color.blue,
			alpha: this.state.color.alpha
		};

		console.log(c);

		//let color = []

		this.setState({ color: c });
	};*/

	render() {
		return (
			<form className="controls">
				<fieldset>
					<h2>Body Color</h2>
					<div className="input-group">
						{/*<input type="range" value={this.state.sliderRed} max="220" min="0" step="1" onChange={ (e) => this.props.onChangeRed(e) } />*/}
						<InputSlider
							className="slider slider-x slider-red"
							axis="x"
							x={this.state.sliderRed}
							xmin={0}
							xmax={220}
							onChange={ this.handleChangeRed }
						/>
						<InputSlider
							className="slider slider-x slider-green"
							axis="x"
							x={this.state.sliderGreen}
							xmin={0}
							xmax={220}
							onChange={ this.handleChangeGreen }
						/>
						<InputSlider
							className="slider slider-x slider-blue"
							axis="x"
							x={this.state.sliderBlue}
							xmin={0}
							xmax={220}
							onChange={ this.handleChangeBlue }
						/>
					</div>
				</fieldset>
				<fieldset>
					<h2>Flame Top</h2>
					<div className="input-group">
						<InputSlider
							className="slider slider-x slider-flame"
							axis="x"
							x={this.state.sliderFlame}
							xmin={0}
							xmax={0.7}
							xstep={0.01}
							onChange={ this.handleChangeFlame }
						/>
					</div>
				</fieldset>
				<fieldset>
					<h2>Hardware</h2>
					<div className="input-group">
						<label className="radio">
							<input type="radio" value="stock" name="hardware" checked={this.state.hwStock} onChange={ (e) => this.handleChangeHardware(e) } />
							<span className="radio-proxy"></span>
							Stock
						</label>
						<label className="radio">
							<input type="radio" value="black" name="hardware" checked={this.state.hwBlack} onChange={ (e) => this.handleChangeHardware(e) } />
							<span className="radio-proxy"></span>
							Black
						</label>
					</div>
				</fieldset>
				<fieldset>
					<h2>Neck Pickup</h2>
					<div className="input-group">
						<label className="radio">
							<input type="radio" value="stock" name="pickupNeck" checked={this.state.pupNeckStock} onChange={ (e) => this.handleChangePickupNeck(e) } />
							<span className="radio-proxy"></span>
							Stock
						</label>
						<label className="radio">
							<input type="radio" value="black" name="pickupNeck" checked={this.state.pupNeckBlack} onChange={ (e) => this.handleChangePickupNeck(e) } />
							<span className="radio-proxy"></span>
							Black
						</label>
						<label className="radio">
							<input type="radio" value="rails" name="pickupNeck" checked={this.state.pupNeckRails} onChange={ (e) => this.handleChangePickupNeck(e) } />
							<span className="radio-proxy"></span>
							Rails
						</label>
						<label className="radio">
							<input type="radio" value="mean90" name="pickupNeck" checked={this.state.pupNeck90} onChange={ (e) => this.handleChangePickupNeck(e) } />
							<span className="radio-proxy"></span>
							Mean 90
						</label>
					</div>
				</fieldset>
				<fieldset>
					<h2>Bridge Pickup</h2>
					<div className="input-group">
						<label className="radio">
							<input type="radio" value="stock" name="pickupBridge" checked={this.state.pupBridgeStock} onChange={ (e) => this.handleChangePickupBridge(e) } />
							<span className="radio-proxy"></span>
							Stock
						</label>
						<label className="radio">
							<input type="radio" value="black" name="pickupBridge" checked={this.state.pupBridgeBlack} onChange={ (e) => this.handleChangePickupBridge(e) } />
							<span className="radio-proxy"></span>
							Black
						</label>
						<label className="radio">
							<input type="radio" value="rails" name="pickupBridge" checked={this.state.pupBridgeRails} onChange={ (e) => this.handleChangePickupBridge(e) } />
							<span className="radio-proxy"></span>
							Rails
						</label>
						<label className="radio">
							<input type="radio" value="mean90" name="pickupBridge" checked={this.state.pupBridge90} onChange={ (e) => this.handleChangePickupBridge(e) } />
							<span className="radio-proxy"></span>
							Mean 90
						</label>
					</div>
				</fieldset>
			</form>
		);
	}

	handleChangeRed = pos => {
		//console.log(pos.x);
 		this.props.onChangeRed(pos.x);
		this.setState({
			sliderRed: pos.x
		});
	};

	handleChangeGreen = pos => {
		//console.log(pos.x);
 		this.props.onChangeGreen(pos.x);
		this.setState({
			sliderGreen: pos.x
		});
	};

	handleChangeBlue = pos => {
		//console.log(pos.x);
 		this.props.onChangeBlue(pos.x);
		this.setState({
			sliderBlue: pos.x
		});
	};

	handleChangeFlame = pos => {
		//console.log(pos.x);
 		this.props.onChangeFlame(pos.x);
		this.setState({
			sliderFlame: pos.x
		});
	};

	handleChangeHardware = e => {
		
		var stockChecked = e.target.value === 'stock' ? true : false;
		var blackChecked = e.target.value === 'black' ? true : false;
		
		this.props.onChangeHardware(e.target.value);

		this.setState({
			hwSelect: e.target.value,
			hwStock: stockChecked,
			hwBlack: blackChecked
		});
		//console.log(this.state.hwSelect);
	};

	handleChangePickupNeck = e => {

		console.log('nk:' + e.target.value);
		
		var stockNeckChecked = e.target.value === 'stock' ? true : false;
		var blackNeckChecked = e.target.value === 'black' ? true : false;
		var railsNeckChecked = e.target.value === 'rails' ? true : false;
		var m90NeckChecked = e.target.value === 'mean90' ? true : false;
		
		this.props.onChangeNeckPup(e.target.value);

		this.setState({
			pupNeck: e.target.value,
			pupNeckStock: stockNeckChecked,
			pupNeckBlack: blackNeckChecked,
			pupNeckRails: railsNeckChecked,
			pupNeck90: m90NeckChecked
		});
		//console.log(this.state.hwSelect);
	};

	handleChangePickupBridge = e => {

		console.log('br:' + e.target.value);
		
		var stockBridgeChecked = e.target.value === 'stock' ? true : false;
		var blackBridgeChecked = e.target.value === 'black' ? true : false;
		var railsBridgeChecked = e.target.value === 'rails' ? true : false;
		var m90BridgeChecked = e.target.value === 'mean90' ? true : false;
		
		this.props.onChangeBridgePup(e.target.value);

		this.setState({
			pupBridge: e.target.value,
			pupBridgeStock: stockBridgeChecked,
			pupBridgeBlack: blackBridgeChecked,
			pupBridgeRails: railsBridgeChecked,
			pupBridge90: m90BridgeChecked
		});
		//console.log(this.state.hwSelect);
	};
}

export default Controls;
