import React, { Component } from 'react';
import Guitar from './Guitar';
import Controls from './Controls';
import GuitarBase from '../images/guitar-base.jpg';

class Customizer extends Component {
	state = {
		color: {
			r: 0,
			g: 0,
			b: 0,
			//a: 1
		},
		flame: 0,
		hardware: 'stock',
		neck: 'stock',
		bridge: 'stock'
	};

	handleRed = (x) => {
		var c = {
			r: x,
			g: this.state.color.g,
			b: this.state.color.b,
			//a: this.state.color.a
		};

		this.setState({ color: c });
	};
	handleGreen = (x) => {
		var c = {
			r: this.state.color.r,
			g: x,
			b: this.state.color.b,
			//a: this.state.color.a
		};

		this.setState({ color: c });
	};
	handleBlue = (x) => {
		var c = {
			r: this.state.color.r,
			g: this.state.color.g,
			b: x,
			//a: this.state.color.a
		};

		this.setState({ color: c });
	};

	handleFlame = (x) => {
		this.setState({ flame:x });
		//console.log(this.state.flame);
	};

	colorChange = () => {
		return this.state.color;
	};

	flameOpacity = () => {
		return this.state.flame;
	};

	handleHardware = (hw) => {
		this.setState({ hardware:hw });
		//console.log(hw);
	};
	changeHardware = () => {
		return this.state.hardware;
	};

	handleNeckPup = (n) => {
		this.setState({ neck:n });
	};
	changeNeckPup = () => {
		return this.state.neck;
	};

	handleBridgePup = (b) => {
		this.setState({ bridge:b });
	};
	changeBridgePup = () => {
		return this.state.bridge;
	};

	render() {
		return (
			<div className="customizer">
				<div className="guitar-container">
					<Guitar 
						onColorChange={this.colorChange}
						onFlameChange={this.flameOpacity}
						onHardwareChange={this.changeHardware}
						onNeckPupChange={this.changeNeckPup}
						onBridgePupChange={this.changeBridgePup}
					/>
					<img className="guitar-base" src={GuitarBase} alt=""/>
					<div className="controls-container">
						<Controls
							onChangeRed={this.handleRed}
							onChangeGreen={this.handleGreen}
							onChangeBlue={this.handleBlue}
							onChangeFlame={this.handleFlame}
							onChangeHardware={this.handleHardware}
							onChangeNeckPup={this.handleNeckPup}
							onChangeBridgePup={this.handleBridgePup}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default Customizer;
