import React, { Component } from 'react';
import GuitarColor from './GuitarColor';
// import GuitarBase from '../images/guitar-base.jpg';
//import Strings from '../images/strings.png';
import FlameTop from '../images/flametop.png';
import Hardware from '../images/hardware-black.png';

class Guitar extends Component {


	render() {

		return (
			<div className="guitar">
				<div className="guitar-part strings"></div>
				<img className="guitar-part flame" src={FlameTop} alt="" style={{opacity: this.props.onFlameChange()}} />
				<img className={this.getHwClasses()} src={Hardware} alt="" />
				<div className={this.getNeckClasses()}></div>
				<div className={this.getBridgeClasses()}></div>
				<GuitarColor
					overlayColor={this.props.onColorChange()}
				/>
			</div>
		);
	}

	getHwClasses(){
		let classes = "guitar-part hardware ";
		classes += this.props.onHardwareChange();
		//console.log(classes);
		return classes;
	}

	getNeckClasses(){
		let classes = "guitar-part pickup neck ";
		classes += this.props.onNeckPupChange();
		//console.log(classes);
		return classes;
	}

	getBridgeClasses(){
		let classes = "guitar-part pickup bridge ";
		classes += this.props.onBridgePupChange();
		//console.log(classes);
		return classes;
	}
}

export default Guitar;
